@import "./reset.css";

.container {
    width: 80%;
    border: 1px solid darkgray;
    margin: 10% auto;
}

.searchField {
    height: 50px;
    width: 30%;
    border: 1px solid gray;

    margin: 20px 0;
    height: 56px;
    width: 50%;
    min-width: 190px;
    background: #ffff;
    border: 1px solid #DDE2E5;
    border-radius: 8px;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    color: #495057;
    line-height: 26px;
    padding: 17px 0 13px 24px;
    outline: none;
    appearance: none;
}

.searchField:focus {
    border: 1px solid #4263EB;
    box-shadow: 0px 1px 1px rgba(51, 154, 240, 0.1);
}

.input-select-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.table {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
    border: 1px solid black;
}

th, td {
    padding: 20px;
}
